

.Item {
	margin: 5px;

	padding-top: 16px;
	padding-left: 20px;
	border: solid 1px #FF4949;
	text-align: left;
	height: 200px;
}
hr {
	margin-right: 10vw;
}
.title {
	font-size: 1.8rem;
	font-weight: bolder;

}
.brand {
	font-size: .9rem;
	font-weight: bold;
	color: #585656;
}
.price {
      font-size: 1.9rem;
	font-weight: bolder;

}

.st {
	float: left;
	margin-right: 5px;


}
.loc {
	width: 20px;
	height: 20px;
	margin-top: 2px;
	margin-right: 5px;
   float: left;
}
.descriptionlabel {
       font-size: 1rem;
	font-weight: bolder;
}
.description {
	font-size: .9rem;
	margin-top: 5px;
	font-family: 'Roboto', sans-serif;
}

.btn {
	border: 1px solid #FF4949;
   background: #FF4949;
   padding: 6px 8px;
   -webkit-border-radius: 7px;
   -moz-border-radius: 7px;
   border-radius: 7px;
   color: white;
   font-size: 15px;
   text-decoration: none;
   vertical-align: middle;
   justify-content: center;
   margin-right: 5px;

}
.btnbox {
    margin-top: 13%;

}

.btn:hover {
	flex :1;
	background: #FE3232;
	color: white;


}
.thumbnailgroup{
	float: left;
}
